// Dependencias
import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Clients extends Component {
    render(){
        return(
            <div id="clients" className="clients-box">
                {/* start clients slider section */}
                <section className="wow fadeIn" style={{ backgroundColor: "#f3f3f3" }}>
                    <div className="container text-center">
                        <div className="row">
                            <div className="swiper-slider-clients swiper-container black-move wow fadeIn">
                                <div className="swiper-wrapper">
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.envato.com"><img src="./assets/images/logo-1.png" alt="" /></Link></div>
                                    {/* end slide */}
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.woocommerce.com"><img src="./assets/images/logo-2.png" alt="" /></Link></div>
                                    {/* end slide */}
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.wordpress.com"><img src="./assets/images/logo-3.png" alt="" /></Link></div>
                                    {/* end slide */}
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.magento.com"><img src="./assets/images/logo-4.png" alt="" /></Link></div>
                                    {/* end slide */}
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.sass-lang.com"><img src="./assets/images/logo-5.png" alt="" /></Link></div>
                                    {/* end slide */}
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.pingdom.com"><img src="./assets/images/logo-6.png" alt="" /></Link></div>
                                    {/* end slide */}
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.lesscss.org"><img src="./assets/images/logo-7.png" alt="" /></Link></div>
                                    {/* end slide */}
                                    {/* start slide */}
                                    <div className="swiper-slide text-center"><Link to="https://www.jquery.com"><img src="./assets/images/logo-8.png" alt="" /></Link></div>
                                    {/* end slide */}
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {/*end clients slider section*/}
            </div>
        )
    }
}

export default Clients;


