// Dependencias
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Components
import ListSocial from './ListSocial';

class FooterNavigation extends Component{
    static propTypes = {
        socialNetworks: PropTypes.array.isRequired
    };

    render(){
        const { socialNetworks } = this.props;
        return(
            <div className="col-md-12 position-absolute top-auto bottom-0 left-0 width-100 padding-20px-bottom xs-padding-15px-bottom">
                <div className="footer-holder">
                    <div className="social-icon-style-8 text-center margin-eleven-bottom padding-eight-top xs-padding-15px-top xs-margin-15px-bottom">
                        <ListSocial items={socialNetworks} className="text-extra-dark-gray" />
                    </div>
                    <div className="text-medium-gray text-extra-small border-top border-color-extra-light-gray padding-twelve-top xs-padding-15px-top">&copy; 2018 GerardoF. Todos los derechos reservados.</div>
                </div>
            </div>            
        )
    }

}

export default FooterNavigation;


