// Dependencias
import React, { Component } from 'react';
// import { Link } from 'react-router-dom';

// Assets
import './assets/css/style.css';

import background1 from '../assets/images/homepage-9-parallax-img7.jpg';

import phpLogo from './assets/img/php.png';
import htmlLogo from './assets/img/html.png';
import bootstrap2Logo from './assets/img/bootstrap_2.png';
import css3_2Logo from './assets/img/css3_2.png';
import javascriptLogo from './assets/img/javascript.png';
import jqueryLogo from './assets/img/jquery.png';
import angularLogo from './assets/img/angular.png';
import cakephpLogo from './assets/img/cakephp.png';
import firebaseLogo from './assets/img/firebase.png';
import lessLogo from './assets/img/less.png';
import mongodbLogo from './assets/img/mongodb.png';
import mysqlLogo from './assets/img/mysql.png';
import nodejsLogo from './assets/img/nodejs.png';
import reactLogo from './assets/img/react.png';
import sassLogo from './assets/img/sass.png';
import sqlserverLogo from './assets/img/sqlserver.png';




const background1Style = {
    backgroundImage: 'url(' + background1 + ')',
};

class SectionServices extends Component {
    render(){
        return (
            <div id="services" className="services-box">
                {/* start services section */}
                <section className=" wow fadeIn color-code parallax xs-background-image-center padding-nineteen-bottom xs-padding-50px-bottom" data-color="3" data-stellar-background-ratio="0.5" style = {background1Style} >
                    <div className="opacity-extra-medium bg-black"></div>
                    <div className="container-fluid padding-thirteen-lr md-padding-six-lr position-relative xs-padding-15px-lr">
                        <div className="row">
                            <div className="col-lg-6 col-md-8 col-sm-12 text-center center-col">                                
                                <h4 className="text-white alt-font font-weight-300 xs-margin-5px-bottom">Ofrecemos servicios de desarrollo web de <strong>alta calidad</strong> y rentables.</h4>
                            </div>
                        </div>
                    </div>
                </section>
                {/* end services section */ }

                {/* start clients section */ }
                <section className="wow color-code fadeIn overlap-section no-padding-top z-index-5" data-color="4">
                    <div className="container-fluid padding-thirteen-lr md-padding-six-lr xs-no-padding-lr">
                        <div className="row">
                            <div className="col-md-12 col-sm-12 col-xs-12">
                                <div className="bg-medium-light-gray gridblock-main pull-left width-100">
                                    <ul className="gridblock-list no-margin no-padding">
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={phpLogo} alt="phpLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={cakephpLogo} alt="cakephpLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={nodejsLogo} alt="nodejsLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={jqueryLogo} alt="jqueryLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={reactLogo} alt="reactLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={angularLogo} alt="angularLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={javascriptLogo} alt="javascriptLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={bootstrap2Logo} alt="bootstrap2Logo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={htmlLogo} alt="htmlLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={css3_2Logo} alt="css3_2Logo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={sassLogo} alt="sassLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={lessLogo} alt="lessLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={mysqlLogo} alt="mysqlLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={sqlserverLogo} alt="sqlserverLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={mongodbLogo} alt="mongodbLogo" /></li>
                                        <li className="margin-five-bottom sm-margin-six-bottom xs-margin-10px-bottom"><img src={firebaseLogo} alt="firebaseLogo" /></li>                                 
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {/* end clients section */ }
            </div>            
        )
    }
}

export default SectionServices;


