// Dependencias
import React, { Component } from 'react';
import { BrowserRouter as Router, Link } from "react-router-dom";

// Assets
import logo from '../../Global/assets/img/logo-gf@b.png';
import logo2x from '../../Global/assets/img/logo-gf@b2x.png';

class SidenavHeader extends Component {
    render() {
        return (
            <Router>
                <div className="col-md-12 col-sm-12 col-xs-12 sidenav-header">
                    {/* start logo */}
                    <div className="logo-holder">
                        <Link to="/" className="display-inline-block logo">
                            <img alt="Logo" src={ logo } data-at2x={ logo2x } />
                        </Link>
                    </div>
                    {/* end logo */}
                    <button className="navbar-toggle mobile-toggle" type="button" id="mobileToggleSidenav">
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                </div>
            </Router>
        )
    }
}

export default SidenavHeader;


