// Dependencias
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Components
import NotFound from './NotFound';
import SectionFooter from './../../HomePage/SidebarWrapper/SectionFooter';

// Assets
// import './assets/css/style.css';

class SidebarWrapper extends Component {
    static propTypes = {
        socialNetworks: PropTypes.array.isRequired
    };
    render() {
        const { socialNetworks } = this.props;

        return (
            <div className="sidebar-wrapper mobile-height bg-background-fade" style={{ paddingLeft: 'unset' }}>
                <NotFound />
                {/* start footer */}
                <SectionFooter socialNetworks={ socialNetworks } />
                {/* end footer */}
            </div>
        )
    }
}

export default SidebarWrapper;


