// Dependencias
import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import background from '../assets/images/homepage-7-slider-img-3.png';


const backgroundStyle = {
    backgroundImage: 'url(' + background + ')',
};

class SectionHome extends Component {
    render() {
        return (
            <div id="home" className="home-box">
                {/* start hero with parallax section */}
                <section className="wow fadeIn no-padding parallax xs-background-image-center" data-stellar-background-ratio="0.5" style={backgroundStyle} >
                    <div className="container full-screen position-relative">
                        <div className="slider-typography">
                            <div className="slider-text-middle-main">
                                <div className="slider-text-middle md-padding-15px-lr">
                                    <div className="col-lg-8 col-sm-10 col-lg-push-4 col-sm-push-1 text-left bg-white-opacity padding-seven-all md-padding-ten-all sm-text-center">
                                        <div className="alt-font text-extra-dark-gray text-uppercase font-weight-700 letter-spacing-minus-2 title-large">Gerardo Forero</div>
                                        <div className="separator-line-horrizontal-full width-100 margin-35px-tb sm-margin-25px-tb xs-margin-20px-tb bg-extra-dark-gray"></div>
                                        <span className="text-dark-gray text-extra-large font-weight-300 margin-35px-bottom display-block sm-margin-25px-bottom xs-margin-15px-bottom">Desarrador Full-Stack con grandes ideas.</span>
                                        <Link to="#about-me" className="btn btn-dark-gray btn-medium section-link">Inicio</Link>
                                    </div>
                                </div>
                                <div className="down-section text-center">
                                    <Link to="#about-me" className="section-link up-down-ani"><i className="ti-mouse icon-small bounce text-deep-pink"></i></Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {/* end hero banner with parallax section */}
            </div>
        )
    }
}

export default SectionHome;


