// Dependencias
import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import background2 from './../assets/images/homepage-9-parallax-img2.jpg';
import image1 from './assets/img/image_1.jpg';
// import './assets/css/style.css';

const background2Style = {
    backgroundImage: 'url(' + background2 + ')',
};

class Portfolio extends Component {
    render() {
        return (
            <div id="portfolio" className="portfolio-box">
                {/* start parallax with video section */}
                <section className="parallax" data-stellar-background-ratio="0.1" style={background2Style}>
                    <div className="opacity-extra-medium bg-black"></div>
                    <div className="container position-relative">
                        <div className="row">
                            <div className="col-lg-6 col-md-6 col-sm-6 text-center center-col">
                                <Link to="https://www.youtube.com/watch?v=uVYFMoxTX_Q" className="popup-youtube"><img src="./assets/images/icon-play.png" className="width-30" alt="" /></Link>
                                <h5 className="alt-font text-white">Que hacemos.</h5>
                            </div>
                        </div>
                    </div>
                </section>
                {/* end parallax with video section */}
                {/* start portfolio section */}
                <section className="bg-extra-dark-gray wow fadeIn md-padding-two-lr sm-no-padding-lr">
                    <div className="container">
                        <div className="row equalize md-equalize-auto">
                            <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12 display-table wow fadeIn">
                                <div className="display-table-cell vertical-align-middle padding-ten-right sm-padding-ten-lr xs-no-padding-lr">
                                    <div className="icon-box width-100 sm-margin-lr-auto xs-width-100">
                                        <div className="icon-box-holder vertical-align-middle display-table-cell position-relative">
                                            <i className="icon-desktop icon-medium text-extra-medium-gray"></i>
                                            <span className="text-large line-height-24 alt-font">Desarrollo.<br />Web.</span>
                                        </div>
                                    </div>
                                    <div className="icon-box width-100 sm-margin-lr-auto xs-width-100">
                                        <div className="icon-box-holder vertical-align-middle display-table-cell position-relative">
                                            <i className="icon-pricetags icon-medium text-extra-medium-gray"></i>
                                            <span className="text-large line-height-24 alt-font">Sistemas.<br />eCommerce.</span>
                                        </div>
                                    </div>
                                    <div className="icon-box width-100 sm-margin-lr-auto xs-width-100">
                                        <div className="icon-box-holder vertical-align-middle display-table-cell position-relative">
                                            <i className="icon-chat icon-medium text-extra-medium-gray"></i>
                                            <span className="text-large line-height-24 alt-font">Sistemas.<br />De informacin.</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12 display-table xs-margin-five-tb wow fadeIn" data-wow-delay="0.2s">
                                <div className="display-table-cell vertical-align-middle text-center sm-padding-ten-right xs-no-padding-right">
                                    <img src={image1} alt="" className="width-100" />
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-12 col-sm-12 col-xs-12 display-table md-margin-seven-top wow fadeIn" data-wow-delay="0.4s">
                                <div className="display-table-cell vertical-align-middle padding-ten-left md-no-padding-lr sm-text-center sm-padding-30px-lr xs-no-padding-lr">
                                    <span className="text-extra-large text-medium-gray alt-font display-block margin-20px-bottom sm-margin-15px-bottom">We're a design studio that believe in the power of ideas.</span>
                                    <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since. Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text Lorem Ipsum has been the industry's standard dummy text.</p>
                                    <Link to="portfolio-full-width-grid-with-icon.html" className="btn btn-very-small btn-transparent-white">View Latest Portfolio</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {/* start portfolio section */}
            </div>
        )
    }
}

export default Portfolio;


