// Dependencias
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Components
import SidenavHeader from './SidenavHeader';
import ListMenu from './ListMenu';
import FooterNavigation from './FooterNavigation';

// Assets
import './assets/css/style.css';

class Navigation extends Component {
    static propTypes = {
        data: PropTypes.object.isRequired
    };
    render() {
        const { data } = this.props;
        const { menu, socialNetworks } = data;
        
        return (
            <nav className="navbar no-margin-bottom bootsnav alt-font bg-white sidebar-nav sidebar-nav-style-1">
                <SidenavHeader />
                <div className="col-md-12 col-sm-12 col-xs-12 no-padding-lr">
                    <div id="navbar-menu" className="collapse navbar-collapse no-padding">
                        <ListMenu items={menu} />
                    </div>
                </div>
                <FooterNavigation socialNetworks={socialNetworks} />
            </nav>
        )
    }
}

export default Navigation;


