// Dependencias
import React, { Component } from 'react';
// import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
// import InstagramEmbed from 'react-instagram-embed';

// Components
import SectionHome from './SectionHome';
import SectionAboutMe from './SectionAboutMe';
import SectionServices from './SectionServices';
import SectionPortfolio from './SectionPortfolio';
import SectionClients from './SectionClients';
import SectionFooter from './SectionFooter';

// Assets
// import background2 from './assets/images/homepage-9-parallax-img2.jpg';
import './assets/css/style.css';

// const background2Style = {
//     backgroundImage: 'url(' + background2 + ')',
// };

class SidebarWrapper extends Component {
    static propTypes = {
        socialNetworks: PropTypes.array.isRequired
    };
    render(){
        const { socialNetworks } = this.props;

        return (
            <div className="sidebar-wrapper mobile-height bg-background-fade">    
                
                
                <SectionHome />
                <SectionAboutMe />
                <SectionServices />                
                <SectionPortfolio />                                  
                <SectionClients />                                                                   

                
                {/* start footer */}
                <SectionFooter socialNetworks={socialNetworks}/>
                {/* end footer */}                
            </div>
        )
    }
}

export default SidebarWrapper;


