// Dependencias
import React, { Component } from 'react';
// import { Link } from 'react-router-dom';
// import PropTypes from 'prop-types';

// Components
// import ListSocial from '../../../Navigation/ListSocial';

// Assets
// import './assets/css/style.css';
// import logo from '../../../Global/assets/img/logo-gf.png';

// style="background-image:url('http://placehold.it/1920x1200');"

import background from '../assets/images/homepage-7-slider-img-3.png';
const backgroundStyle = {
    backgroundImage: 'url(' + background + ')',
};

const btnStyle = {
    marginTop: '25px'
};


class NotFound extends Component {
    render() {
        return (
            <section id="home" className="no-padding parallax mobile-height wow fadeIn" data-stellar-background-ratio="0.5" style={backgroundStyle} >
                <div className="opacity-full bg-extra-dark-gray"></div>
                <div className="container position-relative full-screen">
                    <div className="slider-typography text-center">
                        <div className="slider-text-middle-main">
                            <div className="slider-text-middle">
                                <div className="bg-black-opacity-light width-50 center-col sm-width-80">
                                    <div className="padding-fifteen-all xs-padding-20px-all">
                                        <span className="title-extra-large text-white font-weight-600 display-block margin-30px-bottom xs-margin-10px-bottom">404!</span>
                                        <h6 className="text-uppercase text-deep-pink font-weight-600 alt-font display-block margin-5px-bottom">Pagina No Encontrada</h6>
                                        <span className="text-medium-gray width-80 display-block center-col text-large sm-width-100">La pgina que estabas buscando no se pudo encontrar.</span>
                                        <a href="./" className="btn btn-transparent-white btn-medium text-extra-small border-radius-4" style={btnStyle}>
                                            <i className="ti-arrow-left margin-5px-right no-margin-left" aria-hidden="true"></i> 
                                            Volver a la pgina de inicio
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default NotFound;


